<template src="./template.html"></template>
<script>
import wrongImg from '@/assets/wrong.png';
import jsSHA from 'jssha';

let className = '旅館';
let hostelUrl = `https://ptx.transportdata.tw/MOTC/v2/Tourism/Hotel?%24filter=contains(Class,'${className}')&%24top=20&%24format=JSON`;

export default {
  data() {
    return {
      now: 0,
      data: [],
      clickWait: false,
      timer: {},
    };
  },
  methods: {
    setTime() {
      this.timer = setTimeout(() => {
        this.clickWait = false;
      }, 500);
    },
    stopTime() {
      clearInterval(this.timer);
    },

    change(num) {
      if (this.clickWait) {
        return;
      }
      this.stopTime();
      this.clickWait = true;
      let limit = this.data.length - 1;
      this.now += num;

      if (this.now > limit) {
        this.now = 0;
      } else if (this.now < 0) {
        this.now = limit;
      }
      this.setTime();
    },
    getAuthorizationHeader() {
      //  填入自己 ID、KEY 開始
      let AppID = '87115fa4197645d591dc6280a3be89a0';
      let AppKey = '17xoWssRJ7HDiACPh89qKLE-NXs';
      //  填入自己 ID、KEY 結束
      let GMTString = new Date().toGMTString();
      let ShaObj = new jsSHA('SHA-1', 'TEXT');
      ShaObj.setHMACKey(AppKey, 'TEXT');
      ShaObj.update('x-date: ' + GMTString);
      let HMAC = ShaObj.getHMAC('B64');
      let Authorization =
        'hmac username="' +
        AppID +
        '", algorithm="hmac-sha1", headers="x-date", signature="' +
        HMAC +
        '"';
      return { Authorization: Authorization, 'X-Date': GMTString };
    },
  },
  computed: {
    modifiedData() {
      let array = [...this.data];
      array.forEach((item) => {
        if (item.Picture.PictureUrl1 === undefined) {
          item.Picture.PictureUrl1 = wrongImg;
        }
        if (item.Address.length > 6) {
          item.Address = item.Address.slice(0, 6);
        }
      });
      return array;
    },
    showImg() {
      const start = this.now - 3;
      const array = this.modifiedData
        .slice(start)
        .concat(this.modifiedData.slice(0, start));
      return array;
    },
  },
  mounted() {
    this.axios
      .get(hostelUrl, { headers: this.getAuthorizationHeader() })
      .then((item) => {
        this.data = item.data;
      });
  },
};
</script>
<style lang="scss" scoped>
.slider {
  position: relative;
  overflow: hidden;
  width: 1160px;
  margin: 0 auto;
  margin-bottom: 130px;
  border-radius: 15px;
}
.btn {
  width: 100%;
  box-sizing: border-box;
  display: flex;
  justify-content: center;
  position: absolute;
  top: 50%;
  img {
    margin: 0 590px;
    cursor: pointer;
  }
}
.title {
  width: 768px;
  margin: 0 auto;
  margin-bottom: 72px;
  text-align: center;
  padding: 40px 0;
  border-top: 1px solid #c6c6c6;
  border-bottom: 1px solid #c6c6c6;
  color: #7baebe;
}
.slide-items {
  max-width: 1160px;
  margin: 0 auto;
  display: flex;
  padding: 4px 0;
  box-sizing: border-box;
  margin-left: calc(-1 * 75%);
}
.slideInfo {
  padding: 24px;
  box-sizing: border-box;
  h2 {
    font-size: 20px;
    a {
      color: #7baebe;
    }
  }
  .location {
    display: flex;
    align-items: center;
    margin: 20px 0;
    img {
      width: 20px;
      height: 20px;
    }
  }
}
.slide-item {
  border-radius: 15px;
  margin: 0 8px;
  width: 274px;
  height: 436px;
  display: flex;
  flex-direction: column;
  position: relative;
  box-shadow: 0px 0px 15px #00000029;
  a {
    width: 274px;
    height: 300px;
    overflow: hidden;
    border-radius: 15px;
  }
  .hostelImg {
    width: 100%;
    height: 100%;
    object-fit: cover;
    border-radius: 15px;
    transform: scale(1, 1);
    transition: all 0.3s;
  }
  .hostelImg:hover {
    transform: scale(1.2, 1.2);
  }
  .click {
    width: 87px;
    height: 31px;
    position: absolute;
    display: flex;
    align-items: center;
    justify-content: space-evenly;
    top: 20px;
    right: 0;
    background: #ffffff 0% 0% no-repeat padding-box;
    border-radius: 50px 0px 0px 50px;
  }
}
.slide-item:first-child,
.slide-item:last-child {
  visibility: hidden;
  z-index: -1;
}
.flip-list-move {
  transition: transform 0.6s;
}
</style>
